export const getDifficultyColor = (difficulty: Subject['difficulty']) => {
  switch (difficulty) {
    case 'beginner':
      return 'text-green-600 bg-green-100';
    case 'intermediate':
      return 'text-yellow-600 bg-yellow-100';
    case 'advanced':
      return 'text-red-600 bg-red-100';
    default:
      return 'text-gray-600 bg-gray-100';
  }
};

export const getCategoryColor = (category: Subject['category']) => {
  switch (category) {
    case 'civil':
      return 'bg-purple-100 text-purple-800';
    case 'criminal':
      return 'bg-red-100 text-red-800';
    case 'constitutional':
      return 'bg-blue-100 text-blue-800';
    case 'administrative':
      return 'bg-green-100 text-green-800';
    case 'labor':
      return 'bg-yellow-100 text-yellow-800';
    case 'tax':
      return 'bg-emerald-100 text-emerald-800';
    case 'procedural':
      return 'bg-indigo-100 text-indigo-800';
    default:
      return 'bg-gray-100 text-gray-800';
  }
};

export const getCategoryName = (category: Subject['category']) => {
  switch (category) {
    case 'civil':
      return 'Direito Civil';
    case 'criminal':
      return 'Direito Penal';
    case 'constitutional':
      return 'Direito Constitucional';
    case 'administrative':
      return 'Direito Administrativo';
    case 'labor':
      return 'Direito do Trabalho';
    case 'tax':
      return 'Direito Tributrio';
    case 'procedural':
      return 'Direito Processual';
    default:
      return 'Outros';
  }
}; 